import React from 'react';
import { Table } from 'reactstrap';
import {Button, Container, ButtonGroup} from 'reactstrap';
import { Progress } from 'reactstrap';

export default class TableExample extends React.Component {
  render() {
    return (




      <Table className="mb-0">
           <thead >
          <tr>
            <th color="bg-primary">上傳檔案</th>
          </tr>
        </thead>
        
        <div className="mt-4 mb-4">
            <Button className="mb-2 mr-2" color="primary">選擇檔案</Button>
            <Button className="mb-2 mr-2" color="secondary">PDF</Button>
        </div>
        <div className="mt-4 mb-4">
            <p>上傳檔案的進度</p>
            <Progress className="mb-3 w-100" striped color="info" value={30} />
        </div>
        <thead>
          <tr>
            <th>序號</th>
            <th>檔案名稱</th>
            <th>檔案大小(KB)</th>
            <th>上傳進度</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
        </tbody>
      </Table>
    );
  }
}
